.menu {
  width: 1020px;
  display: grid;
  grid-template-columns: repeat(6, max-content);
  column-gap: 30px;
  align-items: center;
}

.menu li {
  font-family: inherit;
  font-size: 25px;
  line-height: calc(28 / 25 * 100%);
  font-weight: 300;
  letter-spacing: -0.5px;
  font-variant: small-caps;

  &:hover {
    font-size: 25px;
    text-transform: uppercase;
  }

  &:active {
    opacity: 0.5;
  }
}

.menu a {
  color: var(--basic-white);
  text-decoration: none;
}
