.footer-menu {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 40px;
}

.footer-menu__list {
  font-family: inherit;
  font-size: 25px;
  line-height: calc(28 / 25 * 100%);
  color: var(--basic-main);
}

.footer-menu__label {
  line-height: calc(28 / 25 * 100%);
  color: var(--basic-main);
  margin-bottom: 32px;
}

.footer-menu__list a {
  line-height: calc(32 / 25 * 100%);
  color: var(--basic-brand);
}

.footer-menu__contacts {
  display: block;
  margin-top: 30px;
}
