.description {
  width: 100%;
  display: grid;
  grid-template-columns: auto max-content;
  align-items: center;
  column-gap: 12.5vw;
  padding: 0;
  padding-left: 190px;
  padding-right: 190px;

  outline: 2px solid green;
}

.description > section {

}

.description h1 {
  font-family: inherit;
  font-size: 100px;
  line-height: calc(86 / 100 * 100%);
  font-weight: 700;
  color: var(--basic-main);
}

.description p {
  font-family: inherit;
  font-size: 30px;
  line-height: calc(32 / 30 * 100%);
  font-weight: 300;
  color: var(--basic-main);
  letter-spacing: -1.5px;
}

.description p:not(:last-child) {
  margin-bottom: 25px;
}

.description__img {
  display: grid;
  justify-self: end;

}

.description a {
  color: var(--basic-main);
}

.description .button {
  color: var(--basic-text-button);
  text-decoration: none;
}

