.main-nav {
  display: grid;
  grid-template-columns: repeat(3, max-content);
  column-gap: 80px;
  align-items: center;
  padding-left: 120px;
  padding-right: 120px;
}

.main-nav__basket {
  position: relative;
  display: flex;
  width: 37px;
  height: 30px;
  text-decoration: none;
  justify-self: end;
}

.main-nav__basket span {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  top: -12px;
  left: 30px;
  font-size: 17px;
  font-variation-settings: 'wght' 300, 'wdth' 75;
  color: var(--basic-white);
  background-color: var(--basic-main);
  border-radius: 50%;
}

.main-nav__toggle {
  display: none;
  position: absolute;
}
