.nav-footer {
  display: grid;
  grid-template-columns: 1fr 3fr;
  column-gap: 235px;
  padding-top: 53px;
  padding-left: 95px;
  padding-right: 95px;
  padding-bottom: 57px;
  letter-spacing: -0.5px;
}

.nav-footer__copyright {
  margin-bottom: 32px;
}

p.nav-footer__info {
  font-size: 16px;
  line-height: calc(20 / 16 * 100%);
  margin-bottom: 23px;
}

.nav-footer__info a {
  display: inline-block;
  color: var(--basic-main);
  text-decoration: none;
}

.nav-footer__mail {
  font-size: 25px;
  line-height: calc(28 / 25 * 100%);
  color: var(--basic-brand);
}
