.announce {
  padding: 0;
}

.announce__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, auto));
  grid-auto-rows: 368px;
  align-items: center;
}

.announce__item {
  position: relative;
}

.announce__item__img {
  height: 368px;
}

.announce__item__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;

}

.announce__decs {
  display: none;
  position: absolute;
  width: 247px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: var(--basic-main);
  font-family: inherit;
  font-weight: 700;
  font-variant: small-caps;
}

.announce__decs p {
  font-size: 34px;
  line-height: calc(28 / 34 * 100%);
}

.announce__decs .button {
  width: 100%;
  text-decoration: none;
  margin-top: 40px;
}

.announce__item:hover img {
  opacity: 0.3;
}

.announce__item:hover .announce__decs {
  display: block;
}

