.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.logo > div {
  width: 106px;
  height: 106px;
  margin-right: 15px;
}

.logo p {
  font-family: "Eras", "Arial", sans-serif;
  font-size: 25px;
  line-height: calc(28 / 25 * 100%);
  font-weight: 800;
  color: var(--basic-main);
  text-transform: uppercase;
}
