.advantages {
  max-width: 100%;
  background-image: url("../img/advantage-back.jpg");
  background-size: cover;
  color: var(--basic-white);
}

.advantages__wrap  {
  display: grid;
  grid-template-columns: max-content 1fr;
  align-items: center;
}

.advantages h2 {
  display: grid;
  color: var(--basic-white);
  padding-left: 150px;
}

.advantages__list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, minmax(200px, 1fr));
  column-gap: 7vw;
  row-gap: 240px;
  align-items: center;
  justify-content: space-between;
  padding-left: 85px;
  padding-right: 85px;
  margin-top: 217px;
}

.advantages__item {
  position: relative;
}

.advantages__item::before {
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.advantages__item--autonomy::before {
  width: 165px;
  height: 145px;
  top: -160px;
  left: 30px;
  background-image: url("../img/advantage-img-01.png");
}

.advantages__item--service::before {
  width: 196px;
  height: 139px;
  top: -150px;
  left: 30px;
  background-image: url("../img/advantage-img-02.png");
}

.advantages__item--risks::before {
  width: 201px;
  height: 109px;
  top: -127px;
  left: 30px;
  background-image: url("../img/advantage-img-03.png");
}

.advantages__item--expenses::before {
  width: 209px;
  height: 162px;
  top: -175px;
  left: 30px;
  background-image: url("../img/advantage-img-04.png");
}

.advantages__item--demand::before {
  width: 168px;
  height: 125px;
  top: -140px;
  left: 30px;
  background-image: url("../img/advantage-img-05.png");
}

.advantages__item--anticrisis::before {
  width: 148px;
  height: 137px;
  top: -150px;
  left: 30px;
  background-image: url("../img/advantage-img-06.png");
}

.advantages span {
  font-size: 42px;
  font-variation-settings: 'wght' 300, 'wdth' 75;
  margin-bottom: 15px;
}

.advantages__list p {
  font-family: inherit;
  font-variation-settings: 'wght' 300, 'wdth' 75;
  color: var(--basic-white);
  letter-spacing: -0.8px;
}

.advantages .button {
  max-width: 313px;
  text-decoration: none;
  columns: 2;
  margin-left: 75px;
}
