.button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 58px;
  font-family: inherit;
  font-size: 25px;
  line-height: calc(23 / 25 * 100%);
  font-weight: 300;
  text-align: center;
  letter-spacing: -0.3px;
  color: var(--basic-text-button);
  background-color: var(--basic-button);
  font-variant: small-caps;
  padding: 8px 20px;

}
