.catalog-mashines {
  display: grid;
  grid-template-columns: repeat(5, minmax(180px, 384px));
  column-gap: 40px;
  row-gap: 40px;
  justify-content: center;

  outline: 2px solid blue;
}

.catalog-mashines li {
  outline: 2px solid red;

}

.catalog-mashines__link {
  display: flex;
  flex-direction: column;
  height: 100%;
  text-decoration: none;
}

.catalog-mashines__img {
  flex-grow: 1;
  display: flex;
  align-items: flex-end;
  justify-content: center;

  outline: 2px solid green;
}

.catalog-mashines__text {
  min-height: 140px;
  text-align: center;
  margin-top: 40px;
}

.catalog-mashines__text span {
  display: flex;
  flex-direction: column;
}

.catalog-mashines__type {
  font-size: 28px;
  line-height: calc(26 / 30 * 100%);
  font-variation-settings: 'wght' 700, 'wdth' 75;
  letter-spacing: -0.5px;
  font-variant: small-caps;
  margin-bottom: 12px;
}

.catalog-mashines__name {
  font-size: 34px;
  line-height: calc(26 / 34 * 100%);
  font-variation-settings: 'wght' 700, 'wdth' 75;
  font-variant: small-caps;
  margin-bottom: 10px;
}

.catalog-mashines__brand {
  font-size: 25px;
  color: var(--basic-brand);
  font-weight: 300;
}
