.slider-main {
  position: relative;
  max-width: 100%;
  height: 580px;
}

.slider-main__img {
  position: relative;
  width: 100%;
  height: inherit;
}

.slider-main__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.slider-main__desc {
  position: absolute;
  top: 57px;
  left: 50%;
  transform: translateX(-50%);
}

.slider-main__desc h1 {
  margin-bottom: 35px;
}

.slider-main__button {
  max-width: 190px;
  margin-top: 55px;
}
