.crumbs {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding-top: 35px;
  padding-bottom: 55px;

  @media (max-width: 1600px) {
    padding-top: 30px;
    padding-bottom: 48px;
  }

  @media (max-width: 1280px) {
    padding-top: 23px;
    padding-bottom: 38px;
  }

  @media (max-width: 1080px) {
    justify-content: center;
    padding-top: 27px;
    padding-bottom: 42px;
    padding-left: 0;
  }

  @media (max-width: 580px) {
    text-align: center;
  }
}

.crumbs li {
  font-size: clamp(14px, 0.89vw, 17px);
  text-transform: uppercase;
  color: var(--basic-normal-blue);

  @media (max-width: 1080px) {
    font-size: 17px;
  }
}

.crumbs li:last-child {
  margin-left: 5px;
}

.crumbs a {
  color: var(--basic-normal-blue);
  margin-left: 5px;
  margin-right: 5px;
}

.crumbs li:first-child a {
  margin-left: 0;
}

li.crumb-item--current {
  color: var(--back-main);
}

.crumbs--inversion li,
.crumbs--inversion a {
  color: var(--basic-white);
}


