.content {

}

.content h2 {
  font-family: inherit;
  font-size: 83px;
  line-height: calc(87 / 83 * 100%);
  font-variation-settings: 'wght' 700, 'wdth' 75;
  color: var(--basic-main);
}

.content h2 > a {
  color: var(--basic-main);
}

.content p {
  font-family: inherit;
  font-size: 25px;
  line-height: calc(28 / 25 * 100%);
  font-variation-settings: 'wght' 300, 'wdth' 70;
  color: var(--basic-main);
}
