.card-goods {
  display: grid;
  grid-template-columns: repeat(6, minmax(180px, 1fr));
  column-gap: 60px;
  row-gap: 40px;

  outline: 2px solid blue;
}

.card-goods li {
  display: flex;
  flex-direction: column;
  height: 100%;
  text-align: center;

  outline: 2px solid red;
}

.card-goods a {
  cursor: pointer;
}

.card-goods__img {
  position: relative;
  display: flex;
  flex-grow: 1;
  align-items: flex-end;
  justify-content: center;
  cursor: pointer;

  outline: 2px solid green;
}

.card-goods__img::before,
.card-goods__img::after {
  position: absolute;
  z-index: 10;
}

.card-goods__img--hit::before {
  position: absolute;
  content: "";
  width: 118px;
  height: 99px;
  top: 0;
  left: 0;
  background-image: url("../img/hit.svg");
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.card-goods__img--hit::after {
  position: absolute;
  content: "хит продаж";
  font-family: inherit;
  font-size: 25px;
  line-height: calc(18 / 25 * 100%);
  color: var(--basic-white);
  text-align: center;
  top: 30px;
}

.card-goods__img img {
  position: relative;
}

.card-goods__link {
  min-height: 80px;
  font-family: inherit;
  font-size: 23px;
  line-height: calc(28 / 23 * 100%);
  font-variation-settings: 'wght' 300, 'wdth' 70;
  letter-spacing: -0.8px;
  color: var(--basic-brand);
  text-decoration: none;
  margin-top: 18px;
}

.card-goods__price {
  margin-top: 30px;
}
