.container {
  max-width: 1920px;
  padding-left: 40px;
  padding-right: 40px;
  margin: 0 auto;

  outline: 2px solid red;

  @media (max-width: 1600px) {
    max-width: 1600px;

  }

  @media (max-width: 1280px) {
    max-width: 1280px;

  }

  @media (max-width: 992px) {
    max-width: 992px;

  }

  @media (max-width: 767px) {
    max-width: none;

  }
}



