body {
  height: 100%;
  font-family: "Bahnschrift", "Arial", sans-serif;
  font-variation-settings: 'wght' 300, 'wdth' 75;
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  border: none;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

input[name="toggle"] {
  display: none;
}
