.price-card {
  position: relative;
  font-family: inherit;
  font-size: 25px;
  color: var(--basic-white);
  background: transparent;
  cursor: pointer;
}

.price-card::after {
  position: absolute;
  content: "";
  width: 143px;
  height: 50px;
  top: -13px;
  left: 50%;
  transform: translateX(-50%);
  background-image: url("../img/price-shop.svg");
  background-repeat: no-repeat;
  z-index: -1;
}
